<template>
  <div>
    <h1>客户列表</h1>
    <el-form label-position="right"
             inline>
      <el-row>
        <el-form-item label="姓名">
          <el-input v-model="model.name"></el-input>
        </el-form-item>
        <el-form-item label="手机号码">
          <el-input v-model="model.phone"></el-input>
        </el-form-item>
        <el-button size="medium"
                   class="goodsbtnbox"
                   @click="query"
                   type="primary">查询</el-button>
        <el-button size="medium"
                   class="goodsbtnbox"
                   @click="resetQuery">重置</el-button>
      </el-row>
    </el-form>
    <el-table :data="items">
      <!-- <el-table-column prop="_id" label="ID" width="240"></el-table-column> -->
      <el-table-column prop="name" label="客户名称"></el-table-column>
      <el-table-column prop="phone" label="手机号码"></el-table-column>
      <el-table-column prop="sex" label="性别"></el-table-column>
      <el-table-column prop="name" label="头像">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" alt="" style="height:4rem">
        </template>
      </el-table-column>
      <el-table-column prop="address" label="地址"></el-table-column>
      <el-table-column fixed="right" label="操作" width="180">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            @click="$router.push(`/customers/edit/${scope.row._id}`)"
          >编辑</el-button>
          <el-button type="text" size="small" @click="remove(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      model: {
        name: '',
        phone: ''
      },
      items: []
    };
  },
  methods: {
     async query() {
   // const res = await this.$http.post('login', this.model)
      const res = await this.$http.post(`findCustomers`,{name:this.model.name, phone:this.model.phone});
      this.items = res.data;
    },
    resetQuery() {
      this.model = {
        name: '',
        phone: ''
      }
      this.fetch()
    },
    async fetch() {
      const res = await this.$http.get("rest/customers");
      this.items = res.data;
    },
    remove(row) {
      this.$confirm(`是否确定要删除英雄"${row.name}"`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(async () => {
        // eslint-disable-next-line no-unused-vars
        const res = await this.$http.delete(`rest/customers/${row._id}`);
        this.$message({
          type: "success",
          message: "删除成功!"
        });
        this.fetch();
      });
    }
  },
  created() {
    this.fetch();
  }
};
</script>

